/** header **/
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  height: 100%;
  width: 100%;
  max-width: 750px;
  margin: 0 auto;
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  overflow: hidden;
}
.header {
  height: 46px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #fff;
  border-bottom: 1px solid #f5f5f5;
}
.nav-left,
.nav-right {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  top: 0;
  height: 46px;
}
.nav-left {
  left: 0;
  padding-left: 15px;
}
.nav-left .go-back {
  width: 6px;
  height: 10px;
  background: url(data:image/png;base64,);
  background-size: 6px 10px;
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}
.nav-right {
  right: 0;
  padding-right: 15px;
  font-size: 16px;
}
.nav-content {
  max-width: 60%;
  margin: 0 auto;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.add {
  font-size: 20px;
}
/*** footer ***/
