}
.chat-views {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  text-align: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.chat-views .avatar {
  width: 40px;
  height: 40px;
  border: 1px solid #8f8e8e;
  border-radius: 50%;
  display: inline-block;
  margin-right: 15px;
}
.chat-views .message {
  position: relative;
  font-size: 14px;
}
.chat-views .message .chat-log {
  display: block;
  height: 40px;
  line-height: 30px;
  padding: 5px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #fff;
  border-radius: 4px;
}
.chat-views .message .chat-log:after {
  content: "";
  position: absolute;
  left: -5px;
  top: 15px;
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  border-left: 5px solid #fff;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
}
.page,
.chatdialog {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
